:host {
  display: block;
  width: 100%;
  height: 100%;

  .content-header {
    background-color: var(--surface-card);
    border-bottom: 1px solid var(--surface-border);
  }

  .action {
    font-size: 1.2rem;
    padding: 0.5rem;
    border-radius: 50%;
    cursor: pointer;
    transition: all 0.2s ease;

    &.hover:hover {
      background-color: var(--surface-hover);
    }

    &[aria-disabled='true'] {
      opacity: 0.5;
      cursor: not-allowed;
    }
  }
}
